import {
  AfterContentInit,
  AfterViewInit,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  SimpleChanges,
  TemplateRef,
  ViewChild,
  ViewChildren,
  ViewEncapsulation
} from '@angular/core';
import {Directionality} from '@angular/cdk/bidi';
import {animate, keyframes, style, transition, trigger} from '@angular/animations';
import {Subscription} from 'rxjs';
import {debounce, isEmpty} from 'lodash-es';
import {Tree} from 'primeng/tree';
import {PrimeNGConfig, SharedModule, TreeDragDropService, TreeNode} from 'primeng/api';

import {ANIMATION_DURATION, ANIMATION_TIMING_FUNCTION} from '../common/animation';
import {AdaptCustomAction} from '../custom-actions/index';
import {AdaptRadarAngularCustomEventName, AdaptRadarAngularService} from '../common/radar/adapt-radar';
import {AdaptTranslateService} from '../common/i18n/index';
import {KeyValueObject} from '../common/common.models';
import {getFocusableElements} from '../common/focus';

import {AdaptDragDropEventType, AdaptTreeNode, TreeTexts, TreeTrim, TreeWrap} from './tree.model';
import {AdaptTreeNodeTemplateDirective} from './tree-node-template.directive';

let nextUniqueId = 0;

@Component({
  selector: 'adapt-tree',
  templateUrl: './tree.component.html',
  providers: [SharedModule, TreeDragDropService],
  encapsulation: ViewEncapsulation.None,
  animations: [
    trigger('nodeAnimation', [
      transition(':enter', [
          style({opacity: '0', transform: 'translate(-10px, 0)', width: '0', height: '0', overflow: 'hidden'}),
          animate(`${ANIMATION_DURATION.fast} ${ANIMATION_TIMING_FUNCTION.base.transitionInOut}`, keyframes([
            style({
              opacity: '0',
              transform: 'translate(-10px, 0)',
              width: '0',
              height: '0',
              overflow: 'hidden',
              offset: 0
            }),
            style({
              opacity: '0',
              transform: 'translate(-10px, 0)',
              width: '*',
              height: '*',
              overflow: 'hidden',
              offset: 0.1
            }),
            style({opacity: '*', transform: 'translate(0, 0)', width: '*', height: '*', overflow: 'visible', offset: 1})
          ]))
        ]
      ),
      transition(':leave',
        [
          style({opacity: '*', width: '*', height: '*', overflow: 'visible'}),
          animate(`${ANIMATION_DURATION.fast} ${ANIMATION_TIMING_FUNCTION.base.transitionInOut}`, keyframes([
            style({opacity: '*', width: '*', height: '*', overflow: 'visible', offset: 0}),
            style({opacity: '0', width: '*', height: '*', overflow: 'visible', offset: 0.9}),
            style({opacity: '0', width: '0', height: '0', overflow: 'hidden', offset: 1})
          ]))
        ]
      )
    ])
  ]
})
export class AdaptTreeComponent extends Tree implements OnInit, OnChanges, AfterContentInit, AfterViewInit, OnDestroy {
  _uniqueId: string = `adapt-tree-${++nextUniqueId}`;
  treeWrapEnum: typeof TreeWrap = TreeWrap;
  treeTrimEnum: typeof TreeTrim = TreeTrim;

  /**
   * An array of tree nodes
   * @docs-default
   */
  @Input() value: TreeNode[] | AdaptTreeNode[];

  /**
   * Turn on filtering functionality
   * @docs-default false
   */
  @Input() filter: boolean = false;

  /**
   * Configurable text for tree
   * @docs-default {filterPlaceholder: 'Filter', filterBtnClearText: 'Clear', filterNotFound: 'No matches found'}
   */
  @Input() texts: TreeTexts = {};

  /**
   * @docs-default
   */
  @Input() filterBtnClearText: string;

  /**
   * @docs-default
   */
  @Input() filterPlaceholder: string;

  /**
   * String for test id data attribute. For test purposes
   * @docs-default adapt-tree-${++nextUniqueId}
   */
  @Input() testID: string = this._uniqueId;

  /**
   * Toggle lazy load mode. If set to true it enables loader anchor at the bottom of items list.
   * Under the hood IntersectionObserver is used. So please use polyfill if you are supporting old browsers(eg. IE).
   * <a href="https://github.com/w3c/IntersectionObserver/tree/main/polyfill" target="_blank">Link to polyfill</a>
   * @since v10.7.0
   * @docs-default false
   */
  @Input() lazy: boolean = false;

  /**
   * Flag that toggle lazy loader visibility
   * @since v10.7.0
   * @docs-default false
   */
  @Input() lazyLoading: boolean = false;

  /**
   * Label text trim variant. Possible values: Default: text not trimming; Trim: text trimming by container width with ellipsis
   * @docs-default TreeTrim.Default
   * @since v10.15.0
   */
  @Input() trim: TreeTrim = TreeTrim.Default;

  /**
   * Label text wrap variant. Possible values: Default: no wrap text; Wrap: text wrap by words; WrapAll: force text wrap even text has no spaces
   * @docs-default TreeWrap.Default
   * @since v10.15.0
   */
  @Input() wrap: TreeWrap = TreeWrap.Default;

  /**
   * Enables 'select all' button. For the correct work the "node.key" property for each node is required
   * @docs-default false
   * @since v12.13.0
   */
  @Input() selectAllButton: boolean = false;

  /**
   * Enables 'deselect all' button. For the correct work the "node.key" property for each node is required
   * @docs-default false
   * @since v12.13.0
   */
  @Input() deselectAllButton: boolean = false;

  /**
   * Height of the scrollable viewport. Can be css value like 200px, 20rem of 'flex'.
   * 'Flex' value can be useful in cases when viewport should adjust itself according to the parent's height instead of a fixed viewport height.
   * In case of "lazy" true "treeScrollHeight" input has default value "flex"
   * @docs-default
   */
  @Input()
  set treeScrollHeight(value: string | 'flex') {
    this._scrollHeight = value.toLowerCase();
  }

  get treeScrollHeight(): string | 'flex' {
    // For lazy loading case set default scrollHeight value as 'flex'
    return this.lazy ? this._scrollHeight || 'flex' : this._scrollHeight;
  }

  @Input() adaptRadarDisableEventSending: boolean = false;

  /**
   * Scope of the draggable nodes to match a draggableScope.
   * @docs-default
   * @since v12.14.0
   */
  @Input() draggableScope: string;

  /**
   * Scope of the draggable nodes to match a droppableScope.
   * @docs-default
   * @since v12.14.0
   */
  @Input() droppableScope: string;

  /**
   * Whether the nodes are draggable.
   * @docs-default
   * @since v12.14.0
   */
  @Input() draggableNodes: boolean;

  /**
   * Whether the nodes are droppable.
   * @docs-default
   * @since v12.14.0
   */
  @Input() droppableNodes: boolean;

  /**
   * When enabled, drop can be accepted or rejected based on condition defined at onNodeDrop.
   * @docs-default
   * @since v12.14.0
   */
  @Input() validateDrop: boolean;

  /**
   * Callback to invoke when a node is dropped.
   * @docs-default
   * @since v12.14.0
   */
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() readonly onNodeDrop: EventEmitter<AdaptDragDropEventType> = new EventEmitter();

  nodeTouched: boolean;

  dragNodeTree: Tree;

  dragNodeIndex: number;

  dragNodeScope: string | string[];

  dragHover: boolean;

  dragStartSubscription: Subscription;

  dragStopSubscription: Subscription;

  level: number;

  /**
   * An event triggers when lazy load anchor become visible after scrolling list to the bottom
   * @since v10.7.0
   */
  @Output() readonly lazyLoad: EventEmitter<void> = new EventEmitter<void>();

  @ViewChild('treeSearch', {static: false}) treeSearch: ElementRef<Element>;

  @ViewChild('treeContainer', {static: false}) treeContainer: ElementRef<HTMLElement>;

  // Can't use AdaptTreeNodeComponent type here because of circular dependency
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  @ViewChildren('treeNodeComponent') treeNodeComponents: QueryList<any>;

  isRtl: boolean = false;
  dragNode: TreeNode | AdaptTreeNode;
  dragNodeSubNodes: TreeNode[] | AdaptTreeNode[];
  filteredNodes: TreeNode[] | AdaptTreeNode[];
  filterQuery: string = '';
  focusNode: Element;

  filterDebounce: Function = debounce(this.filterWrapper.bind(this), 200);

  @ContentChildren(AdaptTreeNodeTemplateDirective) adaptTemplates: QueryList<AdaptTreeNodeTemplateDirective>;

  private _flattenNodes: AdaptTreeNode[] = [];
  private _flattenFilteredNodes: AdaptTreeNode[] = [];
  private _defaultTexts: TreeTexts = {};
  private _filteredTo: number = 0;
  private _filteredToText: string;
  private _checkAllFilterText: string;
  private _checkAllText: string;
  private _uncheckAllFilterText: string;
  private _uncheckAllText: string;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  private _templates: KeyValueObject<TemplateRef<any>> = {};
  private _scrollHeight: string;
  private _elementIntersectObserver: IntersectionObserver;

  constructor(el: ElementRef,
              dragDropService: TreeDragDropService,
              dir: Directionality,
              public config: PrimeNGConfig,
              private ts: AdaptTranslateService,
              private _adaptRadarService: AdaptRadarAngularService) {
    super(el, dragDropService, config);
    this.setDefaultTexts(ts.getCurrentLanguage());
    this.isRtl = dir.value === 'rtl';
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.selection) {
      this._checkIndeterminateStateRecursive(this.getRootNode());
    }

    if (changes.value) {
      this._flattenNodes = changes.value.currentValue?.length ? this._getFlattenTree(changes.value.currentValue) : [];

      if (!changes.value.firstChange) {
        if (this.treeSearch) {
          this.onClear();
        }

        if (this.draggableNodes) {
          this.updateIcons();
        }
      }
    }
  }

  ngOnInit(): void {
    super.ngOnInit();

    this.texts = {...this._defaultTexts, ...this.texts};

    if (this.droppableNodes) {
      this.dragStartSubscription = this.dragDropService.dragStart$.subscribe(
        event => {
          this.dragNodeTree = event.tree;
          this.dragNode = event.node;
          this.dragNodeSubNodes = event.subNodes;
          this.dragNodeIndex = event.index;
          this.dragNodeScope = event.scope;
        });

      this.dragStopSubscription = this.dragDropService.dragStop$.subscribe(
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        event => {
          this.dragNodeTree = null;
          this.dragNode = null;
          this.dragNodeSubNodes = null;
          this.dragNodeIndex = null;
          this.dragNodeScope = null;
          this.dragHover = false;
        });
    }
  }

  ngAfterContentInit(): void {
    this.adaptTemplates.toArray().forEach(item => {
      this._templates[item.name] = item.template;
    });
  }

  ngAfterViewInit(): void {
    this._elementIntersectObserver = this._adaptRadarService.subscribeOnElementIntersect(this.adaptRadarDisableEventSending, this.el, AdaptRadarAngularCustomEventName.AdaptTree);
  }

  updateIcons(droppedInto?: AdaptTreeNode): void {
    this.value.map((grandParent) => {
      if (grandParent.isGroup && grandParent.children?.length > 0) {
        grandParent.collapsedIcon = 'd-icon-folder';
        grandParent.expandedIcon = 'd-icon-folder_open';
        grandParent.icon = null;

        grandParent.children.map((parent) => {
          if (parent.isGroup && parent.children?.length > 0) {
            parent.collapsedIcon = 'd-icon-folder';
            parent.expandedIcon = 'd-icon-folder_open';
            parent.icon = null;

            parent.children.map((child) => {
              child.icon = 'd-icon-file_o';
              child.children = [];
              child.droppable = false;
            });
          } else if (parent.isGroup && (!parent.children || parent.children?.length === 0)) {
            parent.icon = 'd-icon-folder_o';
          } else {
            parent.icon = 'd-icon-file_o';
            parent.droppable = false;
          }
        });
      } else if (grandParent.isGroup && (!grandParent.children || grandParent.children?.length === 0)) {
        grandParent.icon = 'd-icon-folder_o';
      } else {
        grandParent.icon = 'd-icon-file_o';
        grandParent.droppable = false;
      }
    });

    if (droppedInto) {
      droppedInto.expanded = true;
    }
  }

  ngOnDestroy(): void {
    super.ngOnDestroy();

    if (this._elementIntersectObserver) {
      this._elementIntersectObserver.disconnect();
      this._elementIntersectObserver = null;
    }

    if (this.dragStartSubscription) {
      this.dragStartSubscription.unsubscribe();
    }

    if (this.dragStopSubscription) {
      this.dragStopSubscription.unsubscribe();
    }
  }

  get treeFilterStatus(): string {
    return (this.filterQuery && this.filteredNodes?.length) ?
      `${this._filteredToText} ${this._filteredTo}` : this.texts.filterNotFound;
  }

  get customActions(): AdaptCustomAction[] {
    return [
      {
        name: this.filterQuery ? this._checkAllFilterText : this._checkAllText,
        visible: this.isCheckAllVisible(),
        onClick: this.selectAllOptions.bind(this)
      },
      {
        name: this.filterQuery ? this._uncheckAllFilterText : this._uncheckAllText,
        visible: this.isUnCheckAllVisible(),
        onClick: this.deselectAllOptions.bind(this)
      }
    ];
  }

  get customActionsVisible(): boolean {
    return (this.selectAllButton || this.deselectAllButton) &&
      (this.isMultipleSelectionMode() || this.isCheckboxSelectionMode()) &&
      (this.isCheckAllVisible() || this.isUnCheckAllVisible());
  }

  isCheckAllVisible(): boolean {
    if (!this.selectAllButton) {
      return false;
    }

    if (isEmpty(this.value) || (this.filterQuery && !this.hasFilteredNodes())) {
      return false;
    }

    if (this.hasFilteredNodes()) {
      return this._flattenFilteredNodes
        .filter(node => !node.children?.length)
        .some(node => this.findIndexInSelection(node) < 0);

    } else {
      return this.selection?.length !== this._flattenNodes.length;
    }
  }

  isUnCheckAllVisible(): boolean {
    if (!this.deselectAllButton) {
      return false;
    }

    if (isEmpty(this.value) || (this.filterQuery && !this.hasFilteredNodes())) {
      return false;
    }

    if (this.hasFilteredNodes()) {
      return this._flattenFilteredNodes
        .filter(node => !node.children?.length)
        .some(node => this.findIndexInSelection(node) >= 0);

    } else {
      return this.selection?.length;
    }
  }

  selectAllOptions(): void {
    if (this.hasFilteredNodes()) {
      this._flattenFilteredNodes
        .filter(node => !node.children?.length)
        .forEach(node => {
          node = this.getNodeWithKey(node.key, this.value);

          if (!node) {
            return;
          }

          const index = this.findIndexInSelection(node);

          if (index >= 0) {
            return;
          }

          if (this.propagateSelectionDown) {
            this.propagateDown(node, true);
          } else {
            this.selection = [...this.selection || [], node];
          }

          if (this.propagateSelectionUp && node.parent) {
            this.propagateUp(node.parent, true);
          }
        });

    } else {
      this._flattenNodes
        .forEach(node => {
          node = this.getNodeWithKey(node.key, this.value);

          if (!node) {
            return;
          }

          const index = this.findIndexInSelection(node);

          if (index >= 0) {
            return;
          }

          if (this.propagateSelectionDown) {
            this.propagateDown(node, true);
          } else {
            this.selection = [...this.selection || [], node];
          }

          if (this.propagateSelectionUp && node.parent) {
            this.propagateUp(node.parent, true);
          }
        });
    }

    this.selectionChange.emit(this.selection);
  }

  deselectAllOptions(): void {
    if (this.hasFilteredNodes()) {
      this._flattenFilteredNodes
        .filter(node => !node.children?.length)
        .forEach(node => {
          node = this.getNodeWithKey(node.key, this.value);

          if (!node) {
            return;
          }

          const index = this.findIndexInSelection(node);

          if (index < 0) {
            return;
          }

          if (this.propagateSelectionDown) {
            this.propagateDown(node, false);
          } else {
            this.selection = this.selection.filter((val, i) => i != index);
          }

          if (this.propagateSelectionUp && node.parent) {
            this.propagateUp(node.parent, false);
          }
        });
    } else {
      if (this.propagateSelectionDown) {
        this.value.forEach(node => this.propagateDown(node, false));
      } else {
        this.selection = null;
      }
    }

    this.selectionChange.emit(this.selection);

    getFocusableElements(this.treeContainer.nativeElement)[0]?.focus();
  }

  filterWrapper(newFilterValue: string): void {
    if (this.selectionMode) {
      if (!this.filterQuery && newFilterValue) {
        this.expandAll();

      } else if (this.filterQuery && !newFilterValue) {
        this.collapseAll();
      }
    }

    this.filterQuery = newFilterValue;

    this._filter(this.filterQuery);

    this._flattenFilteredNodes = this.filteredNodes?.length ? this._getFlattenTree(this.filteredNodes) : [];
    this._filteredTo = this.selectionMode ?
      this._flattenFilteredNodes.filter(node => !node.children?.length).length :
      this._flattenFilteredNodes.length;

  }

  setDefaultTexts(translation: KeyValueObject): void {
    this._defaultTexts.filterPlaceholder = translation['adapt.common.filter'];
    this._defaultTexts.filterBtnClearText = translation['adapt.common.clear'];
    this._defaultTexts.filterNotFound = translation['adapt.tree.notFound'];

    this._filteredToText = translation['adapt.select.filteredTo'];
    this._checkAllText = translation['adapt.select.checkAll'];
    this._checkAllFilterText = translation['adapt.select.checkAllFilter'];
    this._uncheckAllText = translation['adapt.select.uncheckAll'];
    this._uncheckAllFilterText = translation['adapt.select.uncheckAllFilter'];
  }

  onNodeClick(event: Event, node: TreeNode | AdaptTreeNode): void {
    super.onNodeClick(event, node);
  }

  onNodeRightClick(event: MouseEvent, node: TreeNode | AdaptTreeNode): void {
    super.onNodeRightClick(event, node);
  }

  findIndexInSelection(node: TreeNode | AdaptTreeNode): number {
    return super.findIndexInSelection(node);
  }

  propagateUp(node: TreeNode | AdaptTreeNode, select: boolean): void {
    super.propagateUp(node, select);
  }

  propagateDown(node: TreeNode | AdaptTreeNode, select: boolean): void {
    super.propagateDown(node, select);
  }

  isSelected(node: TreeNode | AdaptTreeNode): boolean {
    return super.isSelected(node);
  }

  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  getTemplateForNode(node: TreeNode | AdaptTreeNode): TemplateRef<any> {
    if (!isEmpty(this._templates)) {
      return node.type && this._templates[node.type] ? this._templates[node.type] : this._templates.default;
    }

    return null;
  }

  // TODO: Refactor this if possible
  allowDrop(dragNode: AdaptTreeNode, dropNode: AdaptTreeNode, dragNodeScope: string | string[]): boolean {
    return super.allowDrop(dragNode, dropNode, dragNodeScope);
  }

  onClear(): void {
    this.filterQuery = '';
    this._filter(this.filterQuery);
  }

  onLazyLoad(): void {
    // Emit lazy load event only when there is no filter value. Because filtering applied only on already loaded data.
    if (!this.filterQuery) {
      this.lazyLoad.emit();
    }
  }

  trackByNode(index: number, item: AdaptTreeNode): string {
    return item.key;
  }

  expandAll(): void {
    this.value.forEach(node => {
      this._expandRecursive(node, true);
    });
  }

  collapseAll(): void {
    this.value.forEach(node => {
      this._expandRecursive(node, false);
    });
  }

  onDragOver(event: DragEvent): void {
    super.onDragOver(event);
  }

  onDrop(event: DragEvent): void {
    super.onDrop(event);
    this.updateIcons();
  }

  processTreeDrop(dragNode: TreeNode, dragNodeIndex: number): void {
    super.processTreeDrop(dragNode, dragNodeIndex);
  }

  onDragEnter(): void {
    super.onDragEnter();
  }

  onDragLeave(event: DragEvent): void {
    if (this.droppableNodes) {
      const rect = (event.currentTarget as HTMLElement).getBoundingClientRect();

      if (event.x > rect.left + rect.width || event.x < rect.left || event.y > rect.top + rect.height || event.y < rect.top) {
        this.dragHover = false;
      }
    }
  }

  isValidDragScope(dragScope: string | string[]): boolean {
    return super.isValidDragScope(dragScope);
  }

  private _expandRecursive(node: AdaptTreeNode, isExpand: boolean): void {
    node.expanded = isExpand;

    if (node.children) {
      node.children.forEach(childNode => {
        this._expandRecursive(childNode, isExpand);
      });
    }
  }

  private _checkIndeterminateStateRecursive(nodes: TreeNode[], parent?: TreeNode): void {
    if (!nodes) {
      return;
    }

    nodes.forEach(node => {
      if (node.children) {
        this._checkIndeterminateStateRecursive(node.children, node);

        let selectedCount = 0;
        let partialSelected = false;

        for (let i = 0; i < node.children.length; i++) {
          if (node.children[i].partialSelected) {
            partialSelected = true;
            break;
          }

          if (this.findIndexInSelection(node.children[i]) >= 0) {
            selectedCount++;
          }

          if ((selectedCount > 0) && (selectedCount < (i + 1))) {
            partialSelected = true;
            break;
          }
        }

        node.partialSelected = partialSelected;

        if (parent) {
          parent.partialSelected = node.partialSelected;
        }
      }
    });
  }

  private _getFlattenTree(treeData: TreeNode[]): TreeNode[] {
    const flattenTree = [];

    treeData.forEach(node => {
      this._flattenRecursive(node, flattenTree);
    });

    return flattenTree;
  }

  private _flattenRecursive(node: AdaptTreeNode, flattenTree: AdaptTreeNode[]): void {
    flattenTree.push(node);

    if (node.children) {
      node.children.forEach(childNode => {
        this._flattenRecursive(childNode, flattenTree);
      });
    }
  }
}
